import { Layout, Menu } from "antd"
import { NavLink, withRouter } from "react-router-dom";

const HeaderAntd = Layout.Header;

/**
 * Componente que renderiza el Header de la página
 */
export function Header(headerProps:any){
    let {location} = headerProps
    return(
        <HeaderAntd>
          <Menu mode="horizontal" theme="dark" className="header-menu" selectedKeys={[location.pathname.toLocaleLowerCase()]}>
            <Menu.Item key="/aboutme"><NavLink to="/aboutme" >Sobre mí</NavLink></Menu.Item>
            <Menu.Item key="/components"><NavLink to="components">Componentes</NavLink></Menu.Item>
          </Menu>
        </HeaderAntd>
    )
}
export default withRouter(Header);
