
import React, { useEffect } from 'react';
import './App.scss';


import { Layout, notification } from 'antd';


import {Router, Route, Switch, Redirect} from 'react-router-dom';
import { createBrowserHistory} from 'history'

//Header.tsx
/**
 * @packageDocumentation
 * Comentario para Header
 */
import Header from './_master/Header'
import { AboutMe } from './_pages/AboutMe';
import { Footer } from './_master/Footer';
import { ComponentsPage } from './_pages/Components';

const {Content} = Layout;
const history= createBrowserHistory();

export const openNotification=(title:string ,msg: string)=>{
  notification.info({
    message: title,
    description: msg,
    placement:"bottomRight",
    duration: 10
  })
}

/**
 * Componente Principal de la Página
 */
function App() {
  
  return (
    <Router history={history}>
      <Layout className="App">
        <Header/>
        <Layout>
          <Content>
            <Switch>
              <Route exact path="/"><Redirect to="aboutMe"/></Route>
              <Route path="/aboutMe" render={AboutMe}/>
              <Route path="/Components" render={()=><ComponentsPage />} />
            </Switch>
          </Content>
          {/* <Sider  >
          </Sider> */}
        </Layout>

        <Footer/>

      </Layout>
  

    </Router>
  );
}

export default App;
