import { Card, Skeleton } from 'antd';
import Layout, { Content } from 'antd/lib/layout/layout';
import {useEffect} from 'react';
import { openNotification } from '../App';

export function ComponentsPage({}){
    useEffect(()=>{
        openNotification("Página en Construcción","Está Página aún se encuentra en construcción por lo que aun falta información y/o estilos.");
    },[]);
    return <Card className="componentsPage">
        <Skeleton active/>
    </Card>
}